.access-ctrl-aim-root-panel
{
	border: solid 0px red;
	margin:0px;
	width:calc(100%);
	height:calc(100%);
	display:flex;
	flex: 1 1 auto;
	flex-direction:row;
	overflow:hidden;
}
.vm-ext-content-panel-layout
{
	/*border: solid 1px red;*/
	flex: 1 1 auto;
	width:100%;
	display:flex;
	flex-direction:column;
	justify-content:flex-start;
	overflow:hidden;
}
.vm-ext-content-panel-cmds-panel
{
	/*border: solid 1px red;*/
	order:0;
	flex: 0 1 auto;
	width:auto;
}
.vm-ext-content-panel-infobar-panel
{
	/*border: solid 1px red;*/
	order:0;
	flex: 0 0 auto;
}
.vm-ext-content-panel-resource-panel
{
	/*border: solid 1px red;*/
	height:calc(100%);
	overflow:auto;
}
.vm-ext-overview-essentials-panel
{
	border-bottom: solid 1px rgba(128,128,128,0.4);
	padding:2px;
	padding-left:20px;
	padding-right:20px;
	font-family: 'az_SemiBold';
}


