/* tab 1 */
.check-access-tab-layout
{
	border: solid 0px red;
	display:flex;
}
.check-access-left
{
	border: solid 0px red;
	flex: 0 0 360px;
	padding-right:40px;
}
.check-access-right
{
	border: solid 0px red;
	flex: 1 1 auto;
	display:inline-flex;
	justify-content:flex-start;
	margin-top:10px;
	flex-direction:row;
	flex-wrap:wrap;
}

.check-acess-card
{
	border: solid 1px rgba(128,128,128,0.4);
	box-shadow: 0px 4px 8px 0px rgba(128,128,128,0.2);
	flex: 0 0 310px;
	width:310px;
	min-height: 130px;
	margin-bottom:20px;
	margin-right:10px;
	padding:10px;
	display:flex;
	flex-direction:row;
}
.check-acess-card > div:nth-child(1)
{
	/*border: solid 0px red;*/
	flex: 0 0 60px;
	/*margin-right:10px;*/
}
.check-acess-card > div:nth-child(2)
{
	/*border: solid 0px red;*/
	flex: 0 0 220px;
	flex-wrap:wrap;
}

.check-acess-card-row
{
	/*border: solid 1px red;*/
	display:flex;
	margin-top:10px;
}

.check-acess-card-row > div:nth-child(1)
{
	/*border: solid 1px red;*/
	flex: 0 0 50%;
}
.check-acess-card-row > div:nth-child(2)
{
	/*border: solid 1px red;*/
	color: #59B4D9;
	flex: 0 0 50%;
	justify-content:flex-end;
	text-align:right;
}

/* roles tab content style */
.roles-tab-filter-panel
{
	/*border: solid 1px rgba(128,128,128,0.3);*/
	display: flex;
	padding-top:10px;
	padding-bottom:10px;
}
.roles-tab-quick-search
{
	/*border: solid 1px red;*/
	font-size: 13px;
	margin-right: 10px;
}
.roles-tab-select-panel
{ 
	/*border: solid 1px red;*/
	font-size: 13px;
}


.roles-data-table
{
	/*border: solid 1px red;*/
	width:100%;
	margin-top:20px;
}
.roles-data-table > thead > tr > td
{
	/*border: solid 1px red;*/
	border-bottom: solid 1px rgba(128,128,128,1);
	vertical-align: middle;
	padding-top:10px;
	padding-bottom:10px;
	font-size:11px;
	font-family: 'az_SemiBold'
}
.roles-data-table > tbody > tr > td
{
	border-bottom: solid 1px rgba(128,128,128,1);
	vertical-align: middle;
	padding-top:10px;
	padding-bottom:10px;
	font-size:13px;
	font-family: 'az_SemiBold'
}

.roles-data-row-icon
{
	/*border: solid 1px red;*/
	display:inline-block;
	height:20px;
	width:20px;
	margin-right:5px;
	margin-left: 5px;
}
.roles-data-row-icon > svg
{
	height:100%;
	width:100%;
}
.roles-data-row-info-icon
{
	margin-left:5px;
}

/* actions column*/
.roles-data-table > thead > tr > td:nth-child(5)
{
	text-align:center;
}
.roles-data-table > tbody > tr > td:nth-child(5)
{
	text-align:center;
}