/* from azure portal tool kit - blues for light theme
	button-rest : #015cda
	button-hover : #016afe
	button-pressed: #014db7
*/

.cta-btn
{
	border: solid 1px rgba(1, 92, 218, 0.8);
	background-color:  rgba(1, 92, 218, 0.8);
	border-radius:2px;
	color:rgba(255,255,255,1);
	width:auto;
	min-width:84px;
	height:24px;
	display:inline-block;
	vertical-align:top;
	text-align:center;
	padding:20px;
	padding-top:3px;
	margin-right:5px;
	cursor:pointer;
}
.cta-btn:hover
{
	border: solid 1px rgba(1, 92, 218, 1);
	background-color:  rgba(1, 92, 218,1);
}

.cta-inverse-btn
{
	border: solid 1px rgba(1, 92, 218, 0.8);
	background-color: rgba(255,255,255,1);
	border-radius:2px;
	color: rgba(1, 92, 218, 0.8);
	height:24px;
	width:auto;
	min-width:84px;
	vertical-align:top;
	text-align:center;
	text-wrap:avoid;
	padding:20px;
	padding-top:3px;
	cursor:pointer;
	display:inline-block;
	margin-right:5px;

}
.cta-inverse-btn:hover
{
	border: solid 1px rgba(1, 92, 218, 1);
	background-color:  rgba(1, 92, 218, 0.1);
	color:  rgba(1, 92, 218,1);
}

.cta-btn-disabled
{
	border: solid 1px rgba(127, 127, 127, 0.1);
	background-color:  rgba(127, 127, 127, 0.1);
	border-radius:2px;
	color:rgba(127, 127, 127, 0.7);
	height:24px;
	vertical-align:top;
	text-align:center;
	text-wrap:avoid;
	padding:20px;
	padding-top:3px;
	cursor:default;
	display:inline-block;
	min-width:84px;
	margin-right:5px;
}