/* resource parent styles */
.ext-main-content-panel
{
	border: solid 0px purple;
	display: flex;
	flex-direction:row;
	height:calc(100%);
	width:calc(100%);
	overflow:hidden;
}

/* open by default, non-responsive */
.ext-nav-panel
{
	border: solid 0px red;
	width:266px;
	min-width:25px;
	height:calc(100%);
	overflow:auto;
	border-right:solid 1px rgba(128,128,128,0.4);
}
.ext-content-panel
{
	border: solid 0px red;
	height:calc(100%);
	width:calc(100% - 266px);
	display:flex;
	flex-direction:column;
	overflow:auto;
}

