.ext-content-panel-3
{
	border: solid 0px red;
	height:calc(100%);
	width:calc(100%);
	display:flex;
	flex-direction:column;
	overflow:auto;
}
/* hacks for new resource menu placement */
.ext-content-panel-3 > .cmd-panel
{
	/*border: solid 1px red;*/
	padding-left:0px;
}
.ext-content-panel-3 > .cmd-panel > .added-resource-menu
{
	/*border: solid 1px red;*/
	border-right: solid 1px rgba(128,128,128,0.4);
	text-align:center;
	flex: 0 0 auto;
	justify-content:center;
	display:flex;
	padding-left:12px;
}
