@media screen and (max-width: 1023px)
{
	.ext-content-panel-2
	{
		border: solid 0px red;
		height:calc(100%);
		width:calc(100%);
		display:flex;
		flex-direction:column;
		overflow:auto;
	}
	.ext-res-panel-btn
	{
		border: solid 0px red;
		border-right: solid 1px rgba(128,128,128,0.4);
		/*background-color:rgba(0,0,255,0.1);*/
		padding-left:2px;
		/*padding-right:5px;*/
		margin-right:10px;
	}
	.ext-res-panel-overlay
	{
		border: solid 0px red;
		background-color: rgba(255,255,255,1);
		box-shadow: 0px 2px 10px 0px rgba(128,128,128,0.6);
		position:absolute;
		top:112px;
		bottom:0px;
		height:calc(100% - 112px);
		width:320px;
		overflow:auto;
	}
}
@media screen and (max-width: 479 px)
{
	.ext-content-panel-2
	{
		border: solid 1px red;
		height:calc(100%);
		width:calc(100%);
		display:flex;
		flex-direction:column;
		overflow:auto;
	}
	.ext-res-panel-btn
	{
		border: solid 1px red;
		border-right: solid 1px rgba(128,128,128,0.4);
		/*background-color:rgba(0,0,255,0.1);*/
		padding-left:2px;
		/*padding-right:5px;*/
		margin-right:10px;
	}
	.ext-res-panel-overlay
	{
		border: solid 1px red;
		background-color: rgba(255,255,255,1);
		box-shadow: 0px 2px 10px 0px rgba(128,128,128,0.6);
		position:absolute;
		top:112px;
		bottom:0px;
		height:calc(100% - 112px);
		width:auto;
		overflow:auto;
	}
}


/* resource child extension styles, can be in other files*/
.child-ext-root-layout-panel
{
	border: solid 1px red;
	width:calc(100%);
	height:calc(100%);
	overflow:auto;
	display:flex;
	flex-direction:column;
}
.child-ext-essentials-panel
{
	border: solid 0px red;
	order:0;
	width:calc(100%);
	padding:20px;
	flex-direction:row;
	display:flex;
}
.child-ext-content-panel
{
	border: solid 0px red;
	order:1;
	width:calc(100%);
	height:calc(100%);
	overflow:auto;
	display:block;
}

/* extras */
.child-3-content
{
	border: solid 0px red;
	order:1;
	width:calc(100% - 40px);
	height:calc(100% - 40px);
	margin:20px;
	overflow:auto;
	display:block;
}
.child-3-content > div
{
	/*border: solid 1px red;*/
	display:inline-block;
	min-width:50px;
	margin-right:10px;
}