.search-box-panel
{
	/*background-color:rgba(255, 255, 0, 0.50);*/
	padding:3px;
	padding-top:4px;
	margin-top:0px;
	margin-left:0px;
	margin-right:0px;
	margin-bottom:0px;
	min-width:300px;
	max-width:400px;
	height: 100%;
	width:100%;
	vertical-align:top;
	text-align:center;
	display:inline-block;
}
.search-area-panel
{
	border: solid 0px red;
	flex: 0 0 auto;
	vertical-align:top;
	text-align:center;
	width:auto;
}
.search-input-panel
{
	border: solid 0px red;
	display:flex;
	vertical-align:top;
	height:32px;
	width:auto;
	max-width:400px;
	align-self:center;
	background-color:transparent;
}

/* responsive icon action */
.search-resp-icon-close
{
	/*border: solid 1px red;*/
	display:inline-flex;
	cursor:pointer;
}
.search-resp-icon-close > svg
{
	fill: rgba(255,255,255,1);
	height:18px;
	width:18px;
	margin-top:11px;
}
.search-resp-icon-expand
{
	border: solid 0px red;
	display:none;
	cursor:pointer;
}
.search-resp-icon-expand > svg
{
	fill: rgba(255,255,255,1);
	height:18px;
	width:18px;
	margin-top:11px;
}

/* semi-non-responsive */
.search-icon-panel
{
	border: solid 0px red;
	border-top-left-radius:2px;
	border-bottom-left-radius:2px;
	flex: 0 0 24px;
	vertical-align:top;
    background-color: rgba(179, 214, 242, 1);
	padding-top:8px;
	padding-left:4px;
}
.input-box-class 
{
	border: solid 0px red;
	border-top-right-radius:2px;
	border-bottom-right-radius:2px;
	flex: 1 1 auto;
	width:auto;
	min-width:200px;
	vertical-align:top;
    background-color: rgba(179, 214, 242, 1);
	text-align:left;
    font-style: normal;
    color: rgba(0,0,0,1);
	padding:5px;
	display:inline-flex;
}
.input-box-class::placeholder 
{
    font-family: 'Segoe UI', 'Tahoma', Arial;
	font-style: italic;
	font-size:13px;
    color: rgba(128,128,128,1);
}

/* history panel layout */
.search-history-panel
{
	position:absolute;
    top: 38px;
    border: 0px solid #aeaeae;
    border-radius:3px;
    box-shadow: 0px 5px 20px 0px rgba(0,0,0,0.2);
    background-color: #ffffff;
    color: rgba(0,0,0,1);
	flex: 0 0 auto;
	width:400px;
	display:flex;
	flex-direction:column;
    margin: 0px;
	padding:0px;
	text-align:left;
    overflow: auto;
    z-index: 3;
}

.search-history-header
{
	border: solid 0px red;
	display:flex;
	flex-direction:row;
	padding-top:4px;
	padding-bottom:4px;
}
.search-history-header > div:nth-child(1)
{
	border: solid 0px red;
	flex: 0 0 auto;
	font-family: 'az_SemiBold';
	font-size: 13px;
	padding:8px;
	padding-left:17px;
	padding-right:17px;
}
.search-history-header > div:nth-child(2)
{
	border-top: solid 1px rgba(192,192,192,1);
	flex: 1 1 auto;
	display:inline-flex;
	min-height:1px;
	height:1px;
	margin-top:16px;
	margin-right:17px;
}
.search-history-header > div:nth-child(3)
{
	border: solid 0px red;
	flex: 0 0 auto;
	font-size:11px;
	padding:8px;
	padding-left:0px;
	padding-right:17px;
}

.search-history-query-list
{
	border: solid 0px red;
	display:flex;
	flex-direction:column;
}
.search-history-list-item
{
	border: solid 0px red;
	cursor:pointer;
	display:flex;
	flex-direction:row;
	padding:8px;
	padding-left:17px;
	padding-right:17px;
}
.search-history-list-item:hover
{
	background-color:rgba(85,179,255,0.1);
}
.search-history-list-item > div:nth-child(1)
{
	border: solid 0px red;
	flex: 0 0 auto;
}
.search-history-list-item > div:nth-child(1) > svg
{
	height:18px;
	width:18px;
}
.search-history-list-item > div:nth-child(2)
{
	border: solid 0px red;
	flex: 1 2 auto;
	padding-left:8px;
}
.search-history-list-item > div:nth-child(3)
{
	border: solid 0px red;
	flex: 0 0 auto;
	font-size:10px;
	color: rgba(128,128,128,1);
}

/* results panel layout */
.results-panel 
{
    border: 0px solid #aeaeae;
    border-radius:3px;
    box-shadow: 0px 5px 20px 0px rgba(0,0,0,0.2);
    background-color: #ffffff;
    color: rgba(0,0,0,1);
    position: absolute;
    top: 38px;
    width: calc(800px);
	max-height: calc(100% - 300px);
    overflow: hidden;
    margin: 0px;
    z-index: 3;
	display:flex;
	justify-content:flex-start;
	align-content:flex-start;
	align-items:flex-start;
	text-align:left;
	flex-direction:column;
	flex-wrap:nowrap;
}
.results-panel-main-column
{
	border: solid 0px blue;
	order:0;
	display:flex;
	max-height: calc(100% - 100px);
	width:100%;
	padding:0px;
	overflow:auto;
}
.results-quads-column
{
	border: solid 0px red;
	flex: 0 0 50%;
	/*flex-direction:column;*/
    /*flex-wrap: wrap;*/
	/*display:flex;*/
	/*height:100%;*/
	/*width:50%;*/
	overflow: auto;
}

.quadrant
{
	border: solid 0px red;
	text-align:left;
	padding:0px;
	/*flex: 0 0 auto;*/
}

/* search result items */
.quad-no-results
{
	font-size: 11px;
	color: rgba(64,64,64,1);
	font-style:italic;
	padding:8px;
	padding-top:0px;
	padding-left:17px;
	padding-right:17px;
}

/* history & results footer */
.results-panel-footer
{
	border: solid 0px red;
	border-top: 1px solid rgba(128,128,128,0.4);
	order:1;
	text-align:left;
	padding:8px;
	padding-left:17px;
	padding-right:17px;
	font-size:11px;
	width:100%;
}

/* responsive */
@media screen
{
	/*expanded responsive state, show back arrow icon and search box */
	.header-col-two-search > .search-box-panel
	{
		/*border: solid 1px red;*/
		padding:0px;
		width:100%;
		max-width:100%;
		cursor:default;
		display:flex;
	}
	.header-col-two-search > .search-box-panel > .search-resp-icon-close
	{
		/*border: solid 1px red;*/
		display:inline-block;
		width:40px;
		flex: 0 0 40px;
		cursor:pointer;
	}
	.header-col-two-search > .search-box-panel > .search-resp-icon-close:hover
	{
	    background-color: rgba(255,255,255,0.2);
	}
	.header-col-two-search > .search-box-panel > .search-area-panel
	{
		/*border: solid 1px white;*/
		flex: 1 0 auto;
		text-align:center;
		padding-left:5px;
		padding-right:5px;
		padding-top:4px;
		justify-content:flex-end;
		align-content:center;
	}
	.header-col-two-search > .search-box-panel > .search-area-panel > .search-input-panel
	{
		/*border: solid 1px white;*/
		max-width:400px;
		min-width:280px;
		width:100%;
		display:inline-flex;
	}
}
@media screen and (max-width: 1023px)
{
	/* results panel */
	.results-panel 
	{
		width: calc(400px);
		max-height: calc(100% - 100px);
	}
	.results-panel-main-column
	{
		flex-direction:column;
	}
}
@media screen and (max-width: 639px)
{
	/* default responsive state, show search icon */
	.header-col-two > .search-box-panel
	{
		padding:0px;
		width:40px;
		min-width:40px;
		cursor:pointer;
		display:flex;
	}
	.header-col-two > .search-box-panel:hover
	{
	    background-color: rgba(255,255,255,0.2);
	}
	.header-col-two > .search-box-panel > .search-resp-icon-expand
	{
		display:inline-block;
		width:40px;
		flex: 0 0 40px;
	}
	.header-col-two > .search-box-panel > .search-area-panel
	{
		display:none;
	}

	/*expanded responsive state, show back arrow icon and search box */
	.header-col-two-search > .search-box-panel
	{
		/*border: solid 1px red;*/
		padding:0px;
		width:auto;
		max-width:100%;
		cursor:default;
		display:flex;
		flex: 1 1 auto;
	}
	.header-col-two-search > .search-box-panel > .search-resp-icon-close
	{
		/*border: solid 1px red;*/
		display:inline-block;
		width:40px;
		flex: 0 0 40px;
		cursor:pointer;
	}
	.header-col-two-search > .search-box-panel > .search-resp-icon-close:hover
	{
	    background-color: rgba(255,255,255,0.2);
	}
	.header-col-two-search > .search-box-panel > .search-area-panel
	{
		flex: 1 1 auto;
		text-align:center;
		padding-left:5px;
		padding-right:5px;
		padding-top:4px;
		justify-content:flex-end;
		align-content:center;
		width: auto;
	}
	.header-col-two-search > .search-box-panel > .search-area-panel > .search-input-panel
	{
		min-width:270px;
		width:100%;
	}

	/* results panel */
	.results-panel 
	{
		width: calc(400px);
		max-height: calc(100% - 100px);
	}
	.results-panel-main-column
	{
		flex-direction:column;
	}
}
