.ext-header
{
	/*background-color: rgba(255,0,255,0.0);*/
	border-bottom: solid 1px rgba(224,224,224,1);
	height:44px;
	display:flex;
	flex: 0 0 auto;
}

.ext-header-icon
{
	border: solid 0px red;
	flex: 0 0 48px;
}
.ext-header-icon > svg
{
	border: solid 0px red;
	margin-left:14px;
	margin-top:6px;
	height:28px;
	width:28px;
}

.ext-header-text-panel
{
	border: solid 0px red;
	flex: 1 1 auto;
	vertical-align:top;
	padding:0px;
	overflow: hidden;
}
.eht-title
{
	border: solid 0px red;
	font-family: 'az_SemiBold';
	font-size: 18px;
	vertical-align:top;
	margin-top:3px;
	margin-left:5px;
	display:block;
	width:calc(100%);
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
	text-align:left;
}
.eht-subtitle
{
	border: solid 0px red;
	display:block;
	font-family: 'az_SemiBold';
	font-size: 10px;
	color: rgba(100,100,100,1);
	vertical-align:top;
	margin-top:-4px;
	margin-left:5px;
	text-align:left;
}

.ext-header-action-panel
{
	border: solid 0px red;
	flex: 0 1 auto;
	text-align:right;
	vertical-align:top;
	display:flex;
	margin-left:5px;
}

.ext-header-link
{
	/*border: solid 1px lime;*/
	cursor:pointer;
	display:flex;
	flex: 1 1 auto;
	margin-right:5px;
	vertical-align:top;
}
.ext-header-link > svg
{
	/*border: solid 1px red;*/
	flex: 0 0 16px;
	margin-right:4px;
	margin-top:8px;
	height:10px;
	width:10px;
}

.header-doc-link
{
	/*border: solid 1px red;*/
	text-align:right;
	flex: 1 1 auto;
	font-size:13px;
	color:rgba(1, 92, 218, 1.00);
	text-decoration:none;
	padding:6px;
	padding-right:2px;
	padding-top:3px;
}
.header-doc-link:hover
{
	color:rgba(1, 92, 218, 1);
	text-decoration:underline;
}

.header-doc-link-svg
{
	border: solid 0px red;
	height:11px;
	width:11px;
	margin-top:8px;
	margin-left:2px;
}
.header-doc-link-svg > svg
{
	height:100%;
	width:100%;
	fill: rgba(1, 92, 218, 1.00);
}

.ext-header-pin
{
	/*border: solid 1px red;*/
	flex: 0 0 24px;
	height:24px;
	width:24px;
	padding:6px;
	cursor:pointer;
	vertical-align:top;
}
.ext-header-pin > svg
{
	fill: rgba(100,100,100,1);
	height:14px;
	width:14px;
}
.ext-header-pin:hover
{
	background-color: rgba(0, 188, 255, 0.1);
}

.ext-header-close
{
	/*border: solid 1px red;*/
	flex: 0 0 24px;
	height:24px;
	width:24px;
	padding:3px;
	cursor:pointer;
	vertical-align:top;
}
.ext-header-close:hover
{
	background-color: rgba(255,0,0,1);
}
.ext-header-close > svg
{
	/*border: solid 1px red;*/
	fill: rgba(100,100,100,1);
	height:100%;
	width:100%;
	padding:2px;
}
.ext-header-close > svg:hover
{
	fill: rgba(255,255,255,1);
}


@media screen and (max-width: 1023px)
{
	.header-doc-link
	{
		display:none;
	}
}