.info-bar-control
{
	/*border: solid 1px red;*/
	background-color: rgba(128, 128, 128, 0.2);
	border-bottom: solid 1px rgba(128,128,128,0.4);
	color: rgba(0,0,0,1);
	font-size: 12px;
	font-family: 'az_Regular';
	width:100%;
	padding: 5px;
	padding-left:16px;
	padding-right:0px;
	display: flex;
	flex: 1 1 auto;
	flex-direction:row;
	justify-content:flex-start;
}
.info-bar-control-dismissed
{
	display:none;
}
.info-alert
{
	background-color: rgba(208, 0, 0, 0.2);
}
.info-info
{
	background-color: rgba(209, 0, 209, 0.2);
}
.info-warning
{
	background-color: rgba(255, 128, 0,0.2);
}
.info-success
{
	background-color: rgba(211, 255, 209, 0.6);
}

.info-bar-control-icon
{
	/*border: solid 1px red;*/
	flex: 0 0 24px;
	height:auto;
	width:24px;
	justify-content:flex-start;
	vertical-align:top;
}
.info-bar-control-icon > svg
{
	margin-top:-3px;
	vertical-align:top;
	height:100%;
	width:100%;
}

.info-bar-control-content
{
	/*border: solid 1px red;*/
	flex: 1 1 100%;
	height:auto;
	width:100%;
	padding-top:1px;
}
.info-bar-control-header
{
	border: solid 1px red;
}
.info-bar-control-message
{
	/*border: solid 1px blue;*/
	margin-top:2px;
}

.info-bar-control-dismiss
{
	/*border: solid 1px red;*/
	flex: 0 0 22px;
	height:22px;
	width:22px;
	padding-top:3px;
	margin-right:4px;
	justify-content:center;
	text-align:center;
	cursor:pointer;
}
.info-bar-control-dismiss:hover
{
	background-color:rgba(160,0,0,1);
}
.info-bar-control-dismiss:hover > svg
{
	fill: rgba(255,255,255,1);
}

