.auth-panel
{
	/*border: solid 1px red;*/
	position:absolute;
	top:0px;
	left:0px;
	right:0px;
	bottom:0px;
	background-color:rgba(0, 120, 212,1);
	display:flex;
	flex-direction:column;
	justify-content:space-around;
	align-content:center;
	align-items:center;
	text-align:center;
}
.auth-modal-panel
{
	border: solid 1px rgba(0,0,0,0.4);
	border-radius:6px;
	background-color:rgba(255,255,255,1);
	box-shadow: 0px 0px 10px 5px rgba(0,0,0,0.4);
	/*height:250px;*/
	width:400px;
	padding:20px;
	text-align:left;
}

.auth-title
{
	font-family:'az_SemiBold';
	font-size: 18px;
	margin-bottom:20px;
}
.auth-row
{
	/*border: solid 1px red;*/
	margin-bottom:10px;
	font-family:'az_SemiBold';
}
.auth-input
{
	width: 350px;
	height: 32px;
	font-size:14px;
}
.auth-submit-btn
{
	margin-top:10px;
	height:32px;
	width:100px;
	cursor:pointer;
}