.essentials-panel
{
	/*border: solid 1px red;*/
	border: solid 1px transparent;
	border-bottom: solid 1px rgba(0,0,0,0.1);
	margin-bottom:20px;
	min-width:600px;
}
.essentials-panel-content
{
	/*border: solid 1px red;*/
	min-width:600px;
	overflow:auto;
}

.essentials-panel-button
{
	/*border: solid 1px red;*/
	font-family: az_Regular;
	font-size:13px;
	cursor:pointer;
	display:flex;
}
.essentials-panel-button:hover
{
	background-color: rgba(0,0,0,0.02);
}

.essentials-panel-button-open
{
	/*border: solid 1px red;*/
	width:calc(100%);
	height:24px;
	display:flex;
	flex-direction:row;
}

.essentials-panel-button-text
{
	/*border: solid 1px red;*/
	flex: 0 0 100px;
	padding-left:20px;
	padding-right:20px;
	padding-top:2px;
}
.essentials-panel-button-svg
{
	/*border: solid 1px red;*/
	font-family: az_SemiBold;
	font-size:13px;
	flex: 1 1 100%;
	text-align:left;
}
.essentials-panel-button-svg > svg
{
	/*border: solid 1px red;*/
	margin-top:8px;
	margin-left:calc( 50% - 50px);
	transform:rotate(180deg);
}

.essentials-panel-button-closed
{
	/*border: solid 1px blue;*/
	text-align:center;
	width:calc(100%);
	height:24px;
	text-align:center;
}
.essentials-panel-button-closed > svg
{
	margin-top:8px;
}