.help-panel
{
	/*border: solid 1px red;*/
	margin:20px;
	margin-top:10px;
	height: calc(100% - 40px);
	width: calc(100% - 40px);
}

.help-para
{
	border-bottom: solid 1px rgba(190,190,190,1);
	margin-top:10px;
	margin-bottom:10px;
	padding-bottom:10px;
	font-size:12px;
}
.help-para-no-border
{
	margin-top:10px;
	margin-bottom:10px;
	padding-bottom:10px;
	font-size:13px;
}
.help-para-no-border > a
{
	font-size: 11px;
	display:block;
	text-decoration:none;
	color: #015cda;
	margin-bottom:5px;
}