/* BASICS TAB */
.rbac-tabs-panel
{
	/*border: solid 1px red;*/
	padding:20px;
	height:100%;
}

/* BASIC TABS */
.perms-clone-option-panel
{
	/*border: solid 1px red;*/
	width:320px;
}
.select-perms-options
{
	margin-top:10px;
	width:320px;
	padding:3px;
	font-family: 'az_Regular';
	font-size: 13px;
}

/* ACTIONS TAB  */
.actions-panel
{
	/*border: solid 1px red;*/
	display:flex;
	flex-direction:column;
	height:100%;
}
.actions-tab-cmd-panel
{
	/*border: solid 1px red;*/
	border-bottom:solid 1px rgba(128,128,128,0.2);
	padding-left:16px;
	flex: 0 0 auto;
	order:0;
	display:flex;
	margin-top:1px;
}
.actions-content-panel
{
	/*border: solid 1px red;*/
	flex: 1 1 auto;
	display:flex;
	order:1;
	overflow:auto;
}
.actions-content-left
{
	/*border: solid 1px red;*/
	display:block;
	flex: 1 1 auto;
	padding:10px;
	padding-left:20px;
	padding-right:20px;
	overflow:auto;
}
.actions-content-right
{
	/*border-left: solid 1px rgba(128,128,128,0.1);*/
	flex: 0 0 250px;
	padding:10px;
	padding-right:20px;
}

/* JSON Tab */
.json-header-panel
{
	/*border: solid 1px red;*/
	margin-bottom:15px;
}


/* REVIEW TAB */


/* actions-content-left children */
.action-content-block
{
	/*border: solid 1px red;*/
	margin-top:10px;
	margin-bottom:20px;
}
.selected-action-row-header
{
	border-bottom:solid 1px rgba(128,128,128,0.2);
	display:flex;
	margin-top:10px;
	padding-top:5px;
	padding-bottom:10px;
	font-family: 'az_SemiBold';
	font-size:13px;
}
.selected-action-row-header  > div:nth-child(1)
{
	/*border: solid 1px red;*/
	flex: 1 1 auto;
}
.selected-action-row-header  > div:nth-child(2)
{
	/*border: solid 1px red;*/
	flex: 0 0 150px;
}

.selected-action-row
{
	border-bottom:solid 1px rgba(128,128,128,0.2);
	padding-top:10px;
	padding-bottom:10px;
}
.selected-action-row-item
{
	/*border: solid 1px red;*/
	display:flex;
	font-family: 'az_Regular';
	font-size:13px;

}
.selected-action-row-item > div:nth-child(1)
{
	/*border: solid 1px red;*/
	flex: 1 1 auto;
	padding-right:10px;
	width:auto;
	text-overflow:ellipsis;
	white-space:normal; 
	word-break:break-word;
	overflow:visible;
}
.selected-action-row-item > div:nth-child(2)
{
	/*border: solid 1px red;*/
	flex: 0 0 122px;
}
.selected-action-row-item > div:nth-child(3)
{
	/*border: solid 1px red;*/
	flex: 0 0 28px;
	cursor:pointer;
	opacity: 0.6;
	vertical-align:top;
}
.selected-action-row-item > div:nth-child(3) > svg
{
	vertical-align:top;
	/*border: solid 1px red;*/
	height:18px;
}
.selected-action-row-item > div:nth-child(3):hover
{
	opacity: 1;
}

/* action-content-right children */
.actions-help-card
{
	/*border: solid 1px red;*/
	margin-top:10px;
	margin-bottom:10px;
}
.actions-help-card > div:nth-child(1)
{
	/*border: solid 1px red;*/
	font-family: 'az_SemiBold';
	font-size:13px;
}
.actions-help-card > div:nth-child(2)
{
	/*border: solid 1px red;*/
	font-family: 'az_Regular';
	font-size:12px;
}

/* misc */
.empty-actions-add-cta
{
	/*border: solid 1px red;*/
	margin-top:20px;
	padding:20px;
	text-align:center;
}