.cmd-panel
{
	/*border: solid 1px red;*/
	border-bottom: solid 1px rgba(224,224,224,1);
	display:flex;
	flex-wrap:nowrap;
	width:calc(100%);
	height:36px;
	margin:0px;
	padding:0px;
	padding-left:12px;
	vertical-align:top;
	overflow:hidden;
	text-overflow:ellipsis;
}

div.cmd-panel-divider
{
	/*border: solid 1px red;*/
	vertical-align:top;
	margin-top:2px;
	padding:4px;
	font-size:100%;
	color: rgba(128,128,128,1);
}

.cmd-btn-action-panel
{
	display:flex;
}
.cmd-btn-menu-panel
{
	border: solid 1px rgba(128,128,128,0.2);
	position:absolute;
	margin-left:-4px;
	margin-top:28px;
	background-color: rgba(255,255,255,1);
	box-shadow: 0px 5px 10px 0px rgba(0,0,0,0.2)
}

.cmd-btn-with-icon
{
	/*border: solid 1px red;*/
	display:flex;
}

.cmd-btn
{
	border:  1px solid transparent;
	padding:4px;
	cursor:pointer;
	display:flex;
	height:34px;
}
.cmd-btn-text-no-icon
{
	/*border: solid 1px red;*/
	display:flex;
	padding:3px;
	width:100%;
}
.cmd-btn:hover
{
	background-color:#F3F2F1;
}
.cmd-btn:focus
{
	border:  1px solid #605E5C;
}

.cmd-btn-only-icon
{
	border: solid 0px red;
	flex: 0 0 24px;
	min-width:24px;
	padding:5px;
	margin-right:2px;
}
.cmd-btn-only-icon > svg
{
	height:14px;
	width:14px;
	fill: #015cda;
}

.cmd-btn-svg
{
	border: solid 0px red;
	flex: 0 0 16px;
	margin-right:4px;
	margin-left:2px;
}
.cmd-btn-svg > svg
{
	height:16px;
	width:16px;
	margin-top:5px;
	fill: #015cda;
}
.cmd-btn-svg-reverse > svg
{
	border: solid 0px red;
	height:12px;
	width:12px;
	margin-top:7px;
	margin-right:4px;
	margin-left:2px;
	fill: rgba(0,0,0,1);
}

.cmd-btn-text
{
	/*border: solid 1px red;*/
	flex: 1 0 auto;
	padding:0px;
	vertical-align: top;
    padding-top: 3px;
	padding-right:4px;
}
.cmd-btn-text-reverse
{
	/*border: solid 1px red;*/
	flex: 1 0 auto;
	vertical-align: top;
	padding:0px;
	padding-left:1px;
    padding-top: 3px;
	padding-right:4px;
}

.cmd-btn-disabled
{
	color: rgba(192,192,192,1);
	cursor:default;
}
.cmd-btn-disabled:hover
{
	background-color:rgba(85,179,255,0.0);
}
.cmd-btn-disabled > .cmd-btn-svg > svg
{
	fill:rgba(192,192,192,1);
}

@media screen and (max-width: 649px)
{
	/*.cmd-btn-text
	{
		display:none;
	}*/

	.cmd-btn-svg > svg
	{
		height:14px;
		width:14px;
		margin-top:7px;
		opacity:0.8;
	}
}

