.top-bar-btn
{
	/*border: solid 1px red;*/
	background-color: transparent;
	width: 40px;
	height:40px;			
	flex: 0 0 auto;
	cursor:pointer;
	text-align:center;
	padding-top:11px;
}
.top-bar-btn > svg
{
	fill:rgba(255,255,255,1);
	vertical-align:top;
	height:18px;
	width:18px;
}
.top-bar-btn:hover
{
	background-color: rgba(16, 110, 190, 1);
}
.top-bar-btn-selected
{
	background-color: rgba(255,255,255,1);
}
.top-bar-btn-selected:hover
{
	background-color: rgba(242,242,242,1);
}
.top-bar-btn-selected > svg
{
	fill: rgba(0, 120, 212, 0.95);
}

@media screen and (max-width: 1023px)
{
	.top-bar-btn
	{
		/*border: solid 1px red;*/
		background-color: transparent;
		display:block;
		width: 100%;
		height:40px;			
		/*flex: 1 0 auto;*/
		cursor:pointer;
		text-align:left;
		padding-top:11px;
	}
	.top-bar-btn > svg
	{
		fill:rgba(16, 110, 190, 1);
		vertical-align:top;
		height:18px;
		width:18px;
		margin-left:8px;
		margin-right:8px;
	}
	.top-bar-btn:hover
	{
		background-color: rgba(16, 110, 190, 0.2);
	}
	.top-bar-btn-selected
	{
		background-color: rgba(255,255,255,1);
	}
	.top-bar-btn-selected:hover
	{
		background-color: rgba(242,242,242,1);
	}
	.top-bar-btn-selected > svg
	{
		fill: rgba(0, 120, 212, 0.95);
	}
	.top-bar-btn-selected > span
	{
		border: solid 1px red;
	}
}