.mp-ext-render-panel
{
	/*border: solid 1px red;*/
	text-align:left;
	padding:0px;
	display:flex;
	width:100%;
	height:100%;
	overflow:hidden;
}

.mp-category-nav
{
	order:0;
	flex: 0 0 240px;
	/*border: solid 1px blue;*/
	border-right: solid 1px rgba(208,208,208,1);
	overflow:auto;
}

.mp-nav-header
{
	vertical-align: middle;
    font-size: 13px;
    font-family: 'az_SemiBold';
    border-bottom: solid 1px rgba(224,224,224,1);
    padding: 10px;
	padding-left:0px;
	padding-top:8px;
	padding-bottom:8px;
	margin-left:20px;
	margin-right:10px;
    display: block;
}
.mp-nav-item
{
	vertical-align: middle;
    font-size: 12px;
    font-family: 'az_Regular';
    padding: 8px;
	padding-left: 20px;
    display: block;
	cursor:pointer;
}
.mp-nav-item:hover
{
	background-color:rgba(85,179,255,0.1);
}
.mp-nav-item-selected
{
	background-color:rgba(0,192,255,0.2);
	font-weight:600;
}

.mp-content-panel
{
	order:1;
	flex: 1 1 auto;
	/*border: solid 1px green;*/
	padding:0px;
	display:flex;
	flex-direction:column;
}

.mp-filter-panel
{
	/*border: solid 1px red;*/
	border-bottom: solid 1px rgba(208,208,208,1);
	flex: 0 0 auto;
	order:0;
	padding:15px;
	padding-bottom:10px;
}
.mp-selected-category-header
{
	/*border: solid 1px red;*/
	margin-top:10px;
}
.mp-sch-title
{
	font-size:14px;
}
.mp-sch-value
{
	font-size:14px;
	font-weight:700;
}

.mp-data-panel
{
	/*border: solid 1px red;*/
	flex: 1 1 auto;
	order:1;
	padding:15px;
	overflow:auto;
}

.mp-data-card
{
    border: 1px solid rgba(204, 204, 204, 0.5);
    border-radius: 2px;
    box-shadow: 0px 1px 8px 2px rgba(0, 0, 0, 0.1);
    background-color: rgba(255,255,255,1);
    color: rgba(0,0,0,1);
    display: inline-block;
    width: 164px;
    height: 232px;
    margin: 5px;
    margin-bottom: 15px;
    vertical-align: top;
    cursor: pointer;
    padding: 10px;
    transition: all 200ms ease-in-out;
}
.mp-data-card:hover
{
	box-shadow: 0px 2px 8px 2px rgba(0, 0, 0, 0.2);
}

.mp-data-panel-images
{
	display:flex;
}
.mp-card-icon
{
	/*border: solid 1px red;*/
	height:40px;
	width:40px;
	flex: 0 0 40px;
}
.mp-card-badge
{
	/*border: solid 1px red;*/
	flex: 1 0 40px;
	text-align:right;
}

.mp-text-block
{
    /* border: solid 1px red; */
    height: 145px;
    overflow: hidden;
}
.mp-card-display-name
{
	margin-top:5px;
	font-family: 'az_SemiBold';
	font-size:14px;
}
.mp-card-publisher
{
    margin-top: 5px;
    color: #808080;
    font-size: 12px;
}
.mp-card-summary
{
    /* border: solid 1px red; */
    margin-top: 5px;
    color: #595959;
    font-size: 11px;
    height: auto;
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: 14px;
}

.mp-card-social
{
	/*border: solid 1px red;*/
	display: flex;
    vertical-align: top;
    margin-top: 0px;
	font-size:12px;
	margin-top:10px;
}
.mp-star-panel
{
	/*border: solid 1px red;*/
	flex: 0 0 60px;
    vertical-align: top;
	padding-top:2px;
}
.mp-star-rating
{
	/*border: solid 1px red;*/
	flex: 1 1 auto;
    vertical-align: top;
	font-weight:700;
	padding-left:4px;
}
.mp-star-count
{
	/*border: solid 1px red;*/
	flex: 1 1 auto;
    vertical-align: top;
}
.mp-star-fave
{
	/*border: solid 1px red;*/
	flex: 1 1 auto;
	vertical-align:top;
	text-align:right;
	padding-top:1px;
}