.az-info-icon
{
	border: solid 0px red;
	border-radius:7px;
	background-color:rgba(64,64,64,1);
	color:rgba(255,255,255,1);
	width:12px;
	height:12px;
	display:inline-block;
	margin-top:5px;
	margin-left:5px;
	margin-right:5px;
	text-align:center;
	vertical-align:top;
	padding-top:0px;
	font-size:75%;
	font-weight:700;
	cursor:pointer;
}
