/* resource parent styles */
.ext-main-content-panel
{
	border: solid 0px purple;
	display: flex;
	flex-direction:row;
	height:calc(100%);
	width:calc(100%);
	overflow:hidden;
}

/* resource menu open by default, non-responsive */
.ext-nav-panel
{
	border: solid 0px red;
	border-right:solid 1px rgba(128,128,128,0.4);
	flex: 1 1 auto;
	width:auto;
	min-width:25px;
	height:calc(100%);
	overflow:auto;
}
.ext-content-panel
{
	border: solid 0px red;
	flex: 1 1 auto;
	/*width:calc(100% - 266px);*/
	height:calc(100%);
	display:flex;
	flex-direction:column;
	overflow:auto;
}
/* resource menu closed */
.ext-nav-panel-2
{
	border: solid 0px red;
	border-right:solid 1px rgba(128,128,128,0.4);
	flex: 0 1 auto;
	min-width:25px;
	height:calc(100%);
	overflow:auto;
}
.ext-content-panel-2
{
	border: solid 0px red;
	flex: 1 0 auto;
	height:calc(100%);
	display:flex;
	flex-direction:column;
	overflow:auto;
}


/* resource child extension styles, can be in other files*/
/* overview page */
.vm-overview-main-panel
{
	/*border: solid 1px red;*/
	min-width:600px;
	width:calc(100%);
	height:calc(100%);
	overflow:auto;
	display:flex;
	flex-direction:column;
}
.vm-ext-essentials-panel
{
	/*border: solid 1px red;*/
	order:0;
	width:calc(100%);
	padding:20px;
	flex-direction:row;
	display:flex;
	min-width:500px;
	overflow:auto;
}
.vm-ext-essentials-panel-tags
{
	/*border: solid 1px red;*/
	order:1;
	width:calc(100%);
	padding:20px;
	padding-top:0px;
	padding-bottom:0px;
	flex-direction:row;
	display:flex;
	min-width:500px;
	overflow:auto;
}
.vm-ext-data-grid-panel
{
	/*border: solid 1px red;*/
	order:2;
	width:calc(100%);
	height:calc(100%);
	overflow:auto;
	display:block;
}

/* simple essentials style for now */
.essentials-panel-column
{
	/*border: solid 1px red;*/
	flex: 1 1 50%;
	padding-right:5px;
	min-width:250px;
}
.essentials-panel-row
{
	/*border: solid 1px red;*/
	margin-bottom:10px;
	display:flex;
	flex-direction:row;
}
.essentials-panel-cell-left
{
	/*border: solid 1px red;*/
	flex: 0 0 200px;
	color:rgba(96,96,96,1);
}
.essentials-panel-cell-divider
{
	flex: 0 0 10px;
	text-align:center;
}
.essentials-panel-cell-right
{
	/*border: solid 1px red;*/
	flex: 1 1 auto;
	color:rgba(0,0,0,1);
}


@media screen and (max-width: 800px)
{
	.essentials-panel-row
	{
		/*border: solid 1px blue;*/
		margin-bottom:10px;
		/*display:flex;
		flex-direction:column;*/
	}
	.essentials-panel-cell-left
	{
		/*border: solid 1px blue;*/
		flex: 1 1 auto;
		color:rgba(96,96,96,1);
	}
	.essentials-panel-cell-divider
	{
		/*border: solid 1px blue;*/
		flex: 0 0 10px;
		text-align:center;
	}
	.essentials-panel-cell-right
	{
		/*border: solid 1px blue;*/
		flex: 1 1 auto;
		color:rgba(0,0,0,1);
	}
}

span.tag
{
	/*border: solid 1px red;*/
    border-radius: 2px;
    background-color: #d7ecf9;
	color:#000000;
    font-size: 13px;
	padding:3px;
	padding-left:6px;
	padding-right:6px;
	margin-top:-4px;
	margin-right:8px;
	margin-bottom:10px;
	display:inline-block;
	vertical-align:top;
}

/* extras */
.child-3-content
{
	border: solid 0px red;
	order:1;
	width:calc(100% - 40px);
	height:calc(100% - 40px);
	margin:20px;
	overflow:auto;
	display:block;
}
.child-3-content > div
{
	border: solid 0px red;
	display:inline-block;
	min-width:50px;
	margin-right:10px;
}
.child-3-content > div > svg
{
	border: solid 0px red;
	height:64px;
	width:64px;
}