.pill-debug
{
	display:inline-flex;
	height:25px;
	width:auto;
	border: solid 1px rgba(176,176,176,1);
	background-color:rgba(250,250,250,1);
	border-radius:14px;
	margin-right:6px;
	margin-bottom:10px;
	padding:3px;
	padding-left:14px;
	cursor:pointer;
	font-size:12px;
}
.pill-debug:hover
{
	background-color:rgba(255,255,255,1);
	border: solid 1px rgba(32,32,32,1);
}
.pill-final-text
{
	/*border: solid 1px red;*/
	flex:1 1 auto;
	font-weight: 700;
	font-family: 'az_Regular';
}
.pill-close-btn
{
	/*border: solid 1px red;*/
	flex:0 0 14px;
	height:14px;
	width:14px;
	margin-left:10px;
	margin-top:1px;
	margin-right:3px;
}
.pill-close-btn > svg
{
	fill:rgba(128,128,128,1);
}
.pill-close-btn:hover > svg
{
	fill:rgba(64,64,64,1);
}