/* actions panel specific */

.crc-root-panel
{
	border: solid 0px rgba(128,128,128,0);
	margin:0px;
	height:calc(100%);
	max-width: calc(100%);
	overflow:auto;
	padding:0px;
	display:flex;
	flex-direction:column;
}

.crcp-header
{
	border: solid 0px red;
	order: 0;
	flex: 1 0 auto;
	padding:20px;
	padding-bottom:15px;
	box-shadow: 0px 0px 10px 0px rgba(128,128,128,0.2);
}
.crcp-search-box
{
	margin-top:10px;
}

.crcp-column-header-panel
{
	/*border: solid 1px red;*/
	display:flex;
	margin-top:20px;
}
.crcp-column-header-panel > *
{
	/*border: solid 1px red;*/
	flex: 1 1 auto;
	flex-direction:row;
	font-family: 'az_SemiBold';
}

.crcp-data-panel
{
	/*border: solid 1px red;*/
	order: 1;
	flex: 1 1 auto;
	height:calc(100%);
	width:calc(100%);
	overflow-x:hidden;
	overflow-y:auto;
}

/* "?rbacff=true" FeatureFlag == true */
.crcp-back-crumb
{
	color: rgba(0, 120, 212, 0.8);
	text-decoration:none;
	cursor:pointer;
	margin-top:-15px;
	margin-bottom:10px;
}
.crcp-back-crumb:hover
{
	color: rgba(0, 120, 212, 1);
	text-decoration:underline;
}
.crcp-data-tile-panel
{
	/*border: solid 1px red;*/
	padding:20px;
	display:flex;
	/*align-content:space-between;*/
	flex-wrap:wrap;
}
.crcp-data-tile
{
	border: solid 1px rgba(128,128,128,0.1);
	box-shadow: 0px 0px 10px 0px rgba(128,128,128,0.1);
	background-color:rgba(255,255,255,1);
	flex: 0 0 230px;
	cursor:pointer;
	margin-right:15px;
	margin-bottom:15px;
	padding:10px;
	font-family: 'az_SemiBold';
	font-size:14px;
	vertical-align:top;
}
.crcp-tile-desc
{
	margin-top:5px;
	font-family: 'az_Regular';
	font-size:12px;
}
.crcp-data-tile:hover
{
	border: solid 1px rgba(128,128,128,0.4);
	box-shadow: 0px 0px 10px 0px rgba(128,128,128,0.4);
	background-color:rgba(0,160,255,0.1);

}

/* "?rbacff=true" FeatureFlag == false */
.role-group
{
	/*border: solid 0px rgba(255, 128, 0,1);*/
	padding:0px;
	margin-bottom:10px;
}
.role-group-name
{
	border: solid 0px red;
	border-bottom:solid 1px rgba(128,128,128,0.2);
	background-color: rgba(0, 128, 255, 0.05);
	font-size:14px;
	padding-top:10px;
	padding-bottom:10px;
	padding-left:20px;
	padding-right:5px;
	font-family: 'az_SemiBold';
}

.role-type
{
	 /*border: solid 1px blue;*/
}
.role-type-row
{
	/*border: solid 1px red;*/
	width:calc(100%);
}

.role-data-row-header
{
	background-color: rgba(128, 128, 128, 0.06);
	padding:5px;
	padding-left:30px;
	padding-top:10px;
	padding-bottom:10px;
	font-family: 'az_SemiBold';
	border-bottom:solid 1px rgba(128,128,128,0.1);
}
.role-data-row
{
	cursor:pointer;
	border-bottom:solid 1px rgba(128,128,128,0.1);
	padding:5px;
	padding-left:30px;
	padding-top:10px;
	padding-bottom:10px;
	font-family: 'az_Regular';
	font-size:12px;
	display:flex;
	flex: 1 1 auto;
	width:calc(100%);
}
.role-data-row:hover
{
	background-color: rgba(0, 120, 212, 0.05);
}
.role-data-row > div:nth-child(1)
{
	/*border: solid 1px rgba(255,128,128,1);*/
	flex: 0 0 18px;
	order:0;
}
.role-data-row > div:nth-child(2)
{
	/*border: solid 1px rgba(255,128,128,1);*/
	flex: 0 0 50%;
	order:1;
	padding-right:5px;
}
.role-data-row > div:nth-child(3)
{
	/*border: solid 1px rgba(255,128,128,1);*/
	flex: 0 0 50%;
	order:2;
}


/* scopes panel specific */
.scopes-cnxt-layout
{
	border: solid 0px rgba(128,128,128,1);
	order: 1;
	flex: 1 1 auto;
	display:flex;
	flex-direction:row;
	height:calc(100%);
	width:calc(100%);
	overflow-x:hidden;
	overflow-y:auto;
	padding:20px;
	padding-top:10px;
	padding-bottom:0px;
}
.scoped-left-panel
{
	border: solid 0px red;
	flex: 0 0 50%;
	padding-right:15px;
}
.scoped-right-panel
{
	border: solid 0px red;
	border-left: solid 1px rgba(240,240,240,1);
	flex: 0 0 50%;
	padding-left:15px;
}

.scoped-column-header
{
	/*border: solid 1px red;*/
	width:100%;
	font-family: 'az_Regular';
	font-size: 14px;
	margin-bottom:10px;
}

/* subs list */
.scoped-select
{
	font-family: 'az_Regular';
	font-size: 13px;
	width:100%;
	padding:2px;
}
.scoped-selected-panel
{
	border: solid 0px red;
	margin-top:40px;
}
.scoped-selected-panel-header
{
	border: solid 0px red;
	border-bottom: solid 1px rgba(192,192,192,1);
	margin-top:10px;
	padding-bottom:5px;
}

.no-selected-scopes
{
	font-family: 'az_Regular';
	font-style:italic;
	font-size:12px;
	color:rgba(160,160,160,1);
	padding-top:3px;
}
.created-scope-item
{
	border-bottom: solid 1px rgba(192,192,192,1);
	cursor:pointer;
	display:flex;
	padding:5px;
	padding-left:0px;
}
.created-scope-item:hover
{
	background-color:rgba(85,179,255,0.1);
}
.created-scope-item-text
{
	flex: 1 1 auto;
	font-size:12px;
	padding-top:2px;
}
.created-scope-item-delete
{
	border: solid 0px red;
	flex: 0 0 18px;
	margin-left:5px;
}

/* res group list items */
.scoped-res-group-list
{
	border: solid 0px red;
	border-top: solid 1px rgba(192,192,192,1);
	margin-top:10px;
	padding-bottom:5px;
}
.scope-res-group-item
{
	border: solid 0px red;
	border-bottom: solid 1px rgba(192,192,192,1);
	cursor:pointer;
	display:flex;
	padding:5px;
}
.scope-res-group-item:hover
{
	background-color:rgba(85,179,255,0.1);
}
.scope-res-group-icon
{
	border: solid 0px red;
	flex: 0 0 24px;
	margin-right:5px;
}
.scope-res-group-text
{
	border: solid 0px red;
	flex: 1 1 auto;
	font-size:12px;
	padding-top:2px;
}