/* reusable random content style*/
/* HTML generic element styles for Azure */
:focus
{
	outline: dashed 1px #61dafb;
}
::placeholder
{
	font-style:italic;
	font-size:12px;
	color: rgba(128,128,128,1);
}

h1, h2, h3, h4, h5, h6
{
	margin-bottom:10px;
}

a
{
	color: #0078d4;
	text-decoration:none;
}
a:hover
{
	color: #0078d4;
	text-decoration:underline;
}

p
{
	border: solid 0px red;
	text-align:left;
	margin-top:20px;
	margin-bottom:20px;
	padding:0px;
	display:block;
}

code
{
	background-color:rgba(190,190,190,0.2);
	border: solid 1px rgba(190,190,190,0.6);
	vertical-align:middle;
	margin-top:-4px;
	display:inline-block;
	padding-left:5px;
	padding-right:5px;
}
code.code-json
{
	display:inline-block;
	width: 650px;
	height: calc(100% - 30px);
	overflow:auto;
	background-color:rgba(190,190,190,0.2);
	border: solid 1px rgba(190,190,190,0.6);
	padding:5px;
	font-family: 'az_Regular';
	font-size: 14px;
}

ol
{
	border: solid 0px red;
	margin-left:13px;
	margin-bottom:10px;
	list-style:upper-alpha;
}
ol > ol
{
	border: solid 0px red;
	list-style:lower-alpha;
}
ol > ol > ol
{
	border: solid 0px red;
	list-style:lower-roman;
}

ul
{
	border: solid 0px red;
	margin-left:15px;
	margin-bottom:10px;
	list-style:square;
}
ul > ul
{
	border: solid 0px red;
	list-style:circle;
}
ul > ul > ul
{
	border: solid 0px red;
	list-style:disc;
}

ol > li, ul > li
{
	border: solid 0px red;
}

table.demo-table
{
	border: solid 1px rgba(128,128,128,0.4);
	width:100%;
	margin-top:15px;
	margin-bottom:15px;
}
table.demo-table > thead > tr > td
{
	border: solid 0px red;
	border-bottom:solid 1px rgba(128,128,128,0.4);
	background-color:rgba(128,128,128,0.2);
	padding:5px;
	font-family: 'az_SemiBold';
	cursor:pointer;
}
table.demo-table > tbody > tr:nth-child(2n+1)
{
	background-color:rgba(0,128,255,0.1);
}
table.demo-table > tbody > tr > td
{
	border: solid 0px red;
	border-bottom:solid 1px rgba(128,128,128,0.4);
	padding:5px;
	font-family: 'az_Regular';
	cursor:pointer;
}
table.demo-table > tfoot > tr > td
{
	border: solid 0px red;
	background-color:rgba(128,128,128,0.1);
	padding:5px;
	font-family: 'az_Regular';
	font-style:italic;
	font-size: 10px;
	color: rgba(128,128,128,1);
	text-align:right;
}


input[type=text], textarea
{
	font-family:'az_Regular';
	color: rgba(0,0,0,1);
	/* there was an issue with the context panels when set to "z-index:3", so maybe revisit that */
	z-index:1;
}
textarea
{
	min-height:100px;
}

label
{
	/*border: solid 1px red;*/
	margin-right:10px;
	display:inline-flex;
	cursor:pointer;
}
label > input[type=radio]
{
	/*border: solid 1px red;*/
	flex: 0 0 auto;
	margin-top:2px;
	margin-right:4px;
	display:inline-block;
}
label > span
{
	/*border: solid 1px red;*/
	flex: 1 0 auto;
	display:inline-block;
}

