/* rows for create pattern tab panels */
.flex-row-desc
{
	/*border: solid 1px red;*/
	width: 500px;
	margin-bottom:20px;
}
.flex-row-header
{
	/*border: solid 1px red;*/
	font-family: 'az_SemiBold';
	font-family: 14px;
	margin-top:20px;
	margin-bottom:5px;
}

.flex-row
{
	/*border: solid 1px rgba(128,128,128,0.2);*/
	display: flex;
	margin-top:5px;
	margin-bottom:5px;
	padding-top:10px;
	padding-bottom:10px;
}
.flex-row-2
{
	/*border: solid 1px rgba(128,128,128,0.2);*/
	display: flex;
	margin-bottom:5px;
}

.flex-row > div:nth-child(1), .flex-row-2 > div:nth-child(1)
{
	/*border: solid 1px rgba(128,128,128,0.2);*/
	flex: 0 0 300px;
	order:0;
}
.flex-row > div:nth-child(2), .flex-row-2 > div:nth-child(2)
{
	/*border: solid 1px rgba(128,128,128,0.2);*/
	flex: 1 0 auto;
	order:1;
}
.flex-row > div:nth-child(3), .flex-row-2 > div:nth-child(3)
{
	border: solid 1px rgba(255,0,0,1);
	flex: 1 1 auto;
	order:2;
}

