.resource-main-content-panel
{
	/*border: solid 0px red;*/
	margin:0px;
	display:flex;
	flex: 1 1 auto;
	flex-direction:row;
	width:calc(100%);
	height:calc(100%);
	overflow:hidden;
}

.vm-ext-res-toc
{
	/*border: solid 1px rgba(255,0,0,1);*/
	border-right: solid 1px rgba(128,128,128,0.4);
	flex: 0 0 265px;
	display:flex;
	height:auto;
	flex-direction:column;
	justify-content:flex-start;
	/*width:265px;*/
}
.vm-ext-res-toc-collapsed
{
	/*border: solid 1px rgba(255,0,255,1);*/
	border-right: solid 1px rgba(128,128,128,0.4);
	flex: 0 0 24px;
	display:flex;
	height:auto;
	flex-direction:column;
	justify-content:flex-start;
}
.resource-main-content-panel-layout
{

	flex: 1 1 auto;
	width:100%;
	display:flex;
	flex-direction:column;
	justify-content:flex-start;
	overflow:hidden;
}

