.resource-menu-panel-expanded
{
	border: solid 0px red;
	display:flex;
	flex-direction:column;
	justify-content:flex-start;
	width:auto;
	height:100%;
}
.resource-menu-panel-collapsed
{
	border: solid 0px red;
	display:flex;
	flex-direction:column;
	justify-content:flex-start;
	height:100%;
	width:24px;
}

.resource-menu-panel-column
{
	border: solid 0px red;
	display:flex;
	flex-direction:column;
	justify-content:flex-start;
	width:auto;
	height:100%;
}

.resource-menu-search
{
	/*border: solid 1px red;*/
	order: 0;
	flex: 0 0 auto;
	display:flex;
	/*width:auto;*/
	height:44px;
	padding:0px;
}
.resmenu-search
{
	border: solid 0px red;
	flex: 1 0 auto;
	width:auto;
	padding-top:10px;
	padding-bottom:10px;
	padding-left:18px;
	padding-right:16px;
}

.resmenu-toggle
{
	border: solid 0px red;
	flex: 0 0 24px;
	width:24px;
	height:100%;
	text-align:center;
	vertical-align:top;
	padding:0px;
	padding-top:16px;
	cursor:pointer;
}
.resmenu-toggle:hover
{
	background-color: rgba(0,160, 255,0.1);
}
.resmenu-toggle > svg
{
	height:8px;
	width:8px;
}

/* resource menu toggle bar */
.resource-menu-panel-collapsed > .resource-menu-toggle
{
	padding-top:16px;
	text-align:center;
}
.resource-menu-toggle
{
	order:0;
	flex: 0 0 auto;
	display:block;
	width:auto;
	height:100%;
	padding:0px;
	padding-top:3px;
	padding-right:2px;
	min-height:0px;
	text-align:right;
	cursor:pointer;
}
.resource-menu-toggle:hover
{
	background-color: rgba(0,160, 255,0.1);
}
.resource-menu-toggle > svg
{
	height:8px;
	width:8px;
}


.resource-menu-list-panel
{
	/*border: solid 1px rgba(255,0,0,1);*/
	order:1;
	overflow: auto;
	flex: 1 1 auto;
}

/* items */
.resource-menu-list-group {}
.resource-menu-group-name
{
	border-bottom: solid 1px rgba(128,128,128,0.4);
	padding-top:5px;
	padding-bottom:5px;
	margin-left:20px;
	margin-right:20px;
	margin-top:10px;
	font-family: 'az_SemiBold';
	font-size:13px;
}

.resource-menu-item
{
	/*border: solid 1px red;*/
	border-left: solid 4px transparent;
	padding:8px;
	padding-left:16px;
	display:flex;
	cursor:pointer;
	vertical-align:top;
}
/* add left highlight border olnly when in smalled responsive mode , with split menu */
.resource-menu-item-selected
{
	/*border: solid 1px red;*/
	/*border-left: solid 4px #0078D4;*/
	background-color: rgba(238, 238, 238, 0.8);
	padding:8px;
	padding-left:20px;
	display:flex;
	cursor:pointer;
	vertical-align:top;
}
.resource-menu-item:hover
{
	background-color: rgba(238, 238, 238, 1);
}
.resource-menu-item-icon
{
	/*border: solid 1px red;*/
	flex: 0 0 24px;
	display:flex;
	justify-content:flex-start;
}
.resource-menu-item-icon > svg
{
	margin-top:2px;
	height:16px;
	width:16px;
}
.resource-menu-item-name
{
	/*border: solid 1px red;*/
	flex: 1 1 auto;
	vertical-align:top;
}

/* add left highlight border olnly when in smalled responsive mode , with split menu */
@media screen and (max-width: 639px) 
{
	.resource-menu-item
	{
		/*border: solid 1px red;*/
		border-left: solid 4px transparent;
		padding:8px;
		padding-left:20px;
		display:flex;
		cursor:pointer;
		vertical-align:top;
	}

	.resource-menu-item-selected
	{
		/*border: solid 1px red;*/
		border-left: solid 4px #0078D4;
		background-color: rgba(238, 238, 238, 0.8);
		padding:8px;
		padding-left:20px;
		display:flex;
		cursor:pointer;
		vertical-align:top;
	}
}