.all-services-main-panel
{
	/*border: solid 1px red;*/
	height:calc(100%);
	width:calc(100%);
	overflow:auto;
	display:flex;
	flex-direction:column;
	background-color: rgba(255,255,255,1);
	background-image: linear-gradient(180deg, rgba(200,200,200,1) 0px, rgba(249,249,249,0.2) 15px);
}

.all-services-header
{
	/*border: solid 1px red;*/
	padding:20px;
	display:flex;
	flex-direction:row;
}
.all-services-header-text
{
	font-size:18px;
	font-family: 'az_SemiBold';
	margin-right:27px;
}
.all-services-header-search
{
	min-width:180px; 
}

.all-services-content-panel
{
	/*border: solid 1px red;*/
	padding-top:20px;
	padding-left:20px;
	/*padding-right:20px;*/
	height:calc(100%);
	width:calc(100%);
	overflow:auto;
	display:flex;
	flex-direction:row;
}
.all-services-content-category-panel
{
	/*border: solid 1px rgba(128,128,128,0.2);*/
	/*border: solid 1px red;*/
	flex: 0 0 200px;
	width: 200px;
	overflow:auto;
}

.all-services-category-header
{
	font-size:13px;
	font-family: 'az_SemiBold';
	padding:5px;
	padding-left:10px;
	border-bottom: solid 1px rgba(128,128,128,0.5);
	margin-bottom:5px;
	margin-top:10px;
}
.category-nav-item
{
	/*border: solid 1px red;*/
	display:block;
}
.category-nav-item:hover
{
	background-color: rgba(243, 242, 241, 0.6);
	cursor:pointer;
}

.category-nav-item-non-selected
{
	display:flex;
	padding:7px;
	padding-left:10px;
}
.category-nav-item-selected
{
	display:block;
	background-color:#EEEEEE;
	padding:7px;
	padding-left:10px;
	font-weight:600;
}

.all-services-content-list-panel
{
	/*border: solid 1px red;*/
	flex: 1 1 auto;
	margin-left:10px;
	padding-left:10px;
	overflow:auto;
}

/* category panels for the list page */
.category-item-panel
{
	/*border: 1px solid red;*/
	min-width:600px;
	width:auto;
	margin-bottom:20px;
}
.category-header-panel
{
	/*border: solid 1px red;*/
	display:flex;
	flex-direction:row;
	height:24px;
	margin-bottom:20px;
	cursor:pointer;
}
.category-header-panel-text
{
	/*border: solid 1px red;*/
	flex: 0 0 auto;
	font-size: 16px;
	font-family: az_SemiBold;
}
.category-header-panel-text-count
{
	display:inline;
	color:  #323130;
	margin-left:6px;
}
.category-header-panel-spacer
{
	flex: 1 1 auto;
	height:12px;
	border-bottom: 1px solid #EAEAEA;
	margin-left:10px;
	margin-right:10px;
}
.category-header-panel-action-btn
{
	flex: 0 1 auto;
	margin-top:8px;
	margin-right:6px;
	cursor:pointer;
}

.category-header-panel-see-also
{
	/*border: solid 1px red;*/
	flex: 1 1 auto;
	padding-left:16px;
	padding-top:2px;
}
.category-header-panel-see-also-text
{
	/*border: solid 1px red;*/
	color: #0078d4;
	cursor:pointer;
}
.category-header-panel-see-also-text:hover
{
	text-decoration:underline;
}

.category-items-list-panel
{
	/*border: 1px solid #EAEAEA;*/
	/*border: solid 1px red;*/
	display:flex;
	flex-direction:row;
	justify-items:flex-start;
	flex-wrap:wrap;
}
.category-item-list-panel
{
	/*border: solid 1px red;*/
	min-width:300px;
	width:320px;
	max-width:400px;
	margin-bottom:8px;
	margin-right:16px;
	display:flex;
	flex-direction:column;
	/*cursor:pointer;*/
}

/* styles for the overview page */
/* recommended section */
.recommended-panel
{
	/*border: solid 1px red;*/
	margin-bottom:20px;
	margin-right:20px;
	display:flex;
	flex-direction:row;
	flex-wrap:wrap;
}
.rec-icon-panel
{
	border: solid 1px transparent;
	border-radius:2px;
	box-shadow: 0px 0px 2px 0px rgba(128,128,128,0.2);
	flex: 0 0 96px;
	flex-direction:column;
	height:96px;
	width:96px;
	margin-right:15px;
	margin-bottom:10px;
	text-align:center;
	cursor:pointer; 
}
.rec-icon-panel:hover
{
	/*border: solid 1px rgba(128,128,128,0.4);*/
	box-shadow: 0px 0px 4px 0px rgba(128,128,128,0.4);
}
.rec-icon-svg
{
	/*border: solid 1px red;*/
	padding-top:12px;
	padding-bottom:8px;
	height:56px;
}
.rec-icon-svg > svg
{
	/*border: solid 1px red;*/
	height:40px;
	width:40px;
}
.rec-icon-text
{
	/*border: solid 1px red;*/
	padding:0px;
	height:40px;
	padding-top:4px;
	font-size:13px; 
	line-height:100%;
}
.rec-icon-panel > .asset-type-card
{
	text-align:left;
}


/* traning carousel styles */
.carousel-button-panel
{
	/*border: solid 1px red;*/
	display:flex;
	flex: 0 0 48px;
	flex-direction:row;
	margin-right:16px;
}
.carousel-button
{
	/*border: solid 1px red;*/
	flex: 0 0 24px;
	text-align:center;
	padding-top:4px;
}

.gen-training-panel-2
{
	border: solid 1px rgba(128,128,128,0.2);
	display:flex;
	flex-direction:column;
	margin-right:16px;
	margin-bottom:16px;
	width:250px;
	max-width:250px;
	min-height:200px;
	padding-top:16px;
	padding-bottom:20px;
	padding-left:20px;
	padding-right:20px;
	box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.13);
	cursor:pointer;
	transition: all ease-in-out 100ms;
}
.gen-training-panel-2:hover
{
	box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.2);
}

.gen-training-icon-2 
{
	margin-bottom:12px;
}
.gen-training-icon-2 > svg
{
	height:36px;
	width:36px;
}
.training-card-link
{
	color:rgba(0,0,0,1);
	font-size:14px;
	font-family: az_SemiBold;
}
.training-card-time
{
	font-size:13px;
	color: rgba(128,128,128,1);
	margin-bottom:8px;
	margin-top:5px;
}

/* make the most styles */
.make-the-most-header
{
	/*border: solid 1px red;*/
	margin-top:20px;
}
.make-the-most-panel
{
	/*border: solid 1px red;*/
	display:flex;
	flex-direction:row;
	margin-top:20px;
	align-items:flex-start;
	align-content:center;
	flex-wrap:wrap;
}
.make-the-most-item
{
	/*border: solid 1px red;*/
	flex: 1 0 250px;
	background-color: #FAF9F8;
	border-radius:2px;
	width:250px;
	max-width:250px;
	min-height:156px;
	margin-right:16px;
	margin-bottom:16px;
	padding-top:16px;
	padding-bottom:16px;
	padding-left:20px;
	padding-right:20px;
}

.mtm-header
{
	font-size:14px;
	font-family: az_SemiBold;
}
.mtm-desc
{
	margin-top:10px;
	font-size:13px;
	font-family: az_Regular;
}
.mtm-link
{
	margin-top:10px;
	font-size:13px;
	font-family: az_Regular;
}



/* grid styles */
.asset-type-panel
{	
	/*border: solid 1px red;*/
	display:flex;
	flex-direction:row;
	padding:5px;
	cursor:pointer;
	width:100%;
}
.asset-type-panel:hover
{
	background-color:rgba(242,242,242,1);
}
.asset-type-svg
{
	/*border: solid 1px red;*/
	height:18px;
	width:18px;
	margin-right:10px;
}
.asset-type-title-text
{
	/*border: solid 1px red;*/
	font-size: 13px;
	flex:1 0 auto;
}
.asset-type-favorite {}
.asset-type-favorite:hover
{
	cursor:pointer;
}
