/* HTML ROOT */
*
{
	box-sizing:border-box;
	margin: 0px;
	padding: 0px;
	vertical-align:top;
	line-height:normal;
}
body
{
	font-family: 'az_Regular';
	font-size: 13px;
	overflow:hidden;
	background-color:rgba(255,255,255,1);
	color:rgba(0,0,0,1);
	height:calc(100%);
	width:calc(100%);
}
.debug, .debug > *
{
	border: solid 1px red;
}

/* react root element */
#root
{
	position:absolute;
	height:calc(100%);
	width:calc(100%);
	min-width:100%;
	min-height:100%;
	display:grid;
}

.App
{
	border: solid 0px rgba(255,0,0,1);
	/*margin:0px;*/
	overflow:hidden;
	display:flex;
	flex-direction:column;
}
.app-wrapper
{
	/*border: solid 1px red;*/
	height:calc(100%);
}

/* HEADER CONTAINER STYLES */
header
{
	/*border: solid 1px red;*/
	order:0;
	background-color:#0078d4;
	min-height:40px;
	max-height:40px;
	min-width:320px;
	text-align:center;
	display:flex;
	flex-wrap: nowrap;
	flex-direction: row;
	justify-content:space-between;
	box-shadow: 0 6.4px 14.4px 0 rgba(0, 0, 0, 0.132), 0 1.2px 3.6px 0 rgba(0, 0, 0, 0.108);
}

/*header > * { border: solid 1px white; }*/
.header-col-one {
	order: 0;
	flex-grow:1;
	flex-shrink:0;
	flex-basis: auto;
	width:auto;
	text-align:left;
	justify-content:flex-start;
}
.header-col-two {
	order:1;
	flex-grow: 1;
	flex-shrink:0;
	flex-basis:auto;
	width:auto;
	text-align:center;
	justify-content:flex-end;
	align-content:flex-end;
}
.header-col-three {
	order:2;
	flex-grow:0;
	flex-shrink:0;
	flex-basis:auto;
	width:auto;
	text-align:right;
	display:flex;
	justify-content:flex-end;
}

.header-col-two-search
{
	/*border: solid 1px white;*/
	width: 100%;
	display:flex;
}


/*.shell-icon-panel
{
	justify-content:flex-end;
}*/
.toolbar-overflow
{
	display:none;
	padding:0px;
}

.toolbar-overflow-btn
{
	width:40px;
	height:40px;
	padding:0px;
	cursor:pointer;
	background-color:transparent;
	text-align:center;
}
.toolbar-overflow-btn > svg
{
	/*border: solid 1px red;*/
	fill: rgba(255,255,255,1);
	height:16px;
	width:16px;
	margin-top:12px;
}
.toolbar-overflow-btn:hover
{
    background-color: rgba(255,255,255,0.2);
}

.toolbar-overflow-btn-selected
{
	background-color: rgba(255,255,255,1);
}
.toolbar-overflow-btn-selected:hover
{
	background-color: rgba(255,255,255,1);
}
.toolbar-overflow-btn-selected > svg
{
	fill: rgba(0, 120, 212, 0.9);
}

.tool-overflow-menu-panel
{
	/*border: solid 1px white;*/
	background-color: rgba(255,255,255,1);
	box-shadow: 0px 0px 10px 4px rgba(128,128,128,0.4);
	position:absolute;
	left:calc(100% - 260px);
	right:40px;
	padding:0px;
	z-index:2;
}
.tool-menu-panel
{
	/*border: solid 1px white;*/
	width:auto;
	display:flex;
}
.tool-overflow-menu-panel-item
{
	/*border: solid 1px red;*/
	display:flex;
}

/* HEADER COMPONENT STYLES */
/* new hamburder menu */
.nav-btn-div
{
	/*border: solid 0px white;
	background-color: #FF8C00;*/
	width:48px;
	height:100%;
	padding:10px;
	padding-top:8px;
	cursor:pointer;
	display:inline-block;
}
.nav-btn-div > svg
{
	fill: rgba(255,255,255,1);
	margin-top:5px;
	margin-left:6px;
	height:10px;
	width:15px;
}
.nav-btn-div > svg > rect
{
	fill: rgba(255,255,255,1);
}
.nav-btn-div:hover
{
	background-color: rgba(16, 110, 190, 1);
}

/* header - hamburger menu */
.ms-az-brand
{
	/*border: solid 1px white;*/
	/*background-color: #ffba01;*/
    font-family: 'az_SemiBold';
	font-size:16px;
	width:auto;
	min-width:150px;
	height:100%;
    padding: 0px;
	padding-left: 16px;
	padding-top:8px;
	padding-right:4px;
	color: rgba(255,255,255,1);
	cursor:pointer;
	display:inline-block;
}


/* content area */
main
{
	/*border: solid 1px red;*/
	order: 1;
	/*background-color: rgba(255,255,255,0);*/
	/*background-image: linear-gradient(180deg, rgba(,200,200,1) 0px, rgba(255,255,255,1) 20px);*/
	overflow: hidden;
	height: calc(100% - 40px);
	min-width:320px;
	display: flex;
	/*display:none;*/
	flex-wrap: nowrap;
	flex-direction: row;
}
main > *
{
	/*border: solid 1px #61dafb;*/	
	overflow:auto;
	margin:0px;
	flex-shrink:1;
	flex-grow: 1;
	flex-basis: 0;
}

/* also see media queries below */
.nav-panel-default
{
	/*border: solid 1px red;*/
	/*background-color: rgba(0,0,0,1);*/
	min-width:48px;
    flex: 0 1 auto;
    order: 1;
	overflow:hidden;
}
.nav-panel-default-responsive
{
	/*border: solid 1px red;*/
	min-width:0px;
	flex: 0 1 auto;
	order: 1;
	/*display:inline-flex;*/
}


.extension-panel-default
{
	/*border: solid 1px red;*/
	/*width:100%;*/
	/*flex-basis: calc(100%);*/
	/*flex: 0 0 auto;*/
	min-width: 320px;
	max-width:100%;
	width:calc(100%);
	order:2;
	box-shadow: 0px 0px 10px 4px rgba(190,190,190,0.8);
	overflow:auto;
	text-align:left;
	display:flex;
	align-content:flex-start;
	flex-direction:column;
}

/* to be removed when "context-panel" component is finished */
.context-panel-default
{
	/*border: solid 1px red;*/
	position:absolute;
	right:0px;
	top:40px;
	bottom:0px;
	width:  100%;
	max-width:480px;
	min-width:320px;
	order:3;
	background-color:rgba(255,255,255,1);
	/*box-shadow: 0px 10px 10px 4px rgba(0,0,0,0.4);*/
	box-shadow: 0 25.6px 57.6px 0 rgba(0, 0, 0, 0.22), 0 4.8px 14.4px 0 rgba(0, 0, 0, 0.18);
	z-index:1;
	display:flex;
	flex-direction:column;
	overflow:auto;
}
.cpd-600
{
	width:  600px;
	max-width:600px;
}
.cpd-800
{
	width:  800px;
	max-width:800px;
}

.context-panel-header
{
	/*border: solid 1px red;*/
	order: 0;
	flex: 0 0 auto;
	display:flex;
	height:32px;
}

.cp-current-icon
{
	/*border:solid 1px red;*/
	order:0;
	flex: 0 0 24px;
	width:100%;
	text-align:center;
	vertical-align:top;
	padding:4px;
	margin-left:15px;
}
.cp-current-icon > svg
{
	/*border:solid 1px red;*/
	height:100%;
	width:100%;
}

.cp-current-title
{
	/*border: solid 1px red;*/
	order:1;
	flex: 0 1 100%;
	padding:5px;
	padding-top:3px;
	text-align:left;
	font-size: 18px;
	font-family: 'az_SemiBold';
	vertical-align:top;
}
.cp-close-btn
{
	/*border: solid 1px red;*/
	order:2;
	flex: 0 0 24px;
	height:24px;
	cursor:pointer;
	padding-top:3px;
	text-align:center;
}
.cp-close-btn:hover
{
	/*border: solid 1px blue;*/
	background-color:rgba(190,0,0,1);
}
.cp-close-btn:hover svg
{
	fill: rgba(255,255,255,1);
}

.context-panel-content
{
	/*border: solid 1px lime;*/
	order:1;
	height:calc(100%);
	width:calc(100%);
	flex: 1 1 auto;
	overflow:auto;
}
.context-panel-button-panel
{
	order: 2;
	flex: 1 0 auto;
	border-top: solid 1px rgba(128,128,128,0.2);
	box-shadow: 10px 10px 10px 10px rgba(128,128,128,0.2);
	padding:20px;
	height:auto;
}

/* responsive*/
@media screen and (max-width: 1023px)
{
	/* header > * { border: solid 1px white; }*/
	.header-col-one {
		flex-grow:0;
		flex-shrink:0;
		flex-basis: auto;
		width:auto;
	}
	.header-col-two {
		flex-grow: 1;
		flex-shrink:1;
		flex-basis:auto;
		width:auto;
		justify-content:center;
		align-content:center;
		align-items:center;
		text-align:center;
	}
	.header-col-three {
		flex-grow:0;
		flex-shrink:0;
		flex-basis:auto;
		width:auto;
		min-width:unset;
	}

	/* context buttons and panel */
	.toolbar-overflow
	{
		display:inline;
		padding:0px;
	}
	.tool-menu-panel
	{
		border: solid 1px rgba(255,255,255,1);
		display:none;
	}

	/* reflow for resp */
}
@media screen and (max-width: 639px)
{
	/*header > * { border: solid 1px white; }*/

	.header-col-one {
		flex-grow:1;
		flex-shrink:0;
		flex-basis: auto;
		width:auto;
	}
	.header-col-two {
		flex-grow: 0;
		flex-shrink:0;
		flex-basis:auto;
		justify-content:flex-end;
	}
	.header-col-three {
		flex-grow:0;
		flex-shrink:1;
		flex-basis:auto;
		width:auto;
	}

	/* context buttons and panel */
	.toolbar-overflow
	{
		display:inline;
		padding:0px;
	}
	.tool-menu-panel
	{
		border: solid 1px rgba(255,255,255,1);
		display:none;
	}

	/* new main nav behavior */
	.nav-panel-default
	{
		/*border: solid 1px red;*/
		min-width:0px;
	}
}
@media screen and (max-width: 600px)
{
	.context-panel-default
	{
		/*border: solid 1px red;*/
		max-width:600px;
	}
}
