/* 8/25/19 responsive*/
.main-navigation-panel
{
	border: solid 0px blue;
	padding:0px;
	display:flex;
	flex-direction:column;
	height:100%;
}

/* default styles */
.main-nav-open
{
	border: solid 0px red;
	background-color:rgba(249, 249, 249, 0.2);
	width:225px;
	min-width:225px;
	max-width:225px;
	height:calc( 100% );
	display:flex;
	flex-direction:column;
	overflow:hidden;
	z-index:4;
}
.main-nav-closed 
{
	/*border: solid 1px red;*/
	width: 48px;
	max-width:48px;
	min-width:48px;
	height: calc(100%);
}
.main-nav-pinned
{
	/*border: solid 1px red;*/
	position:absolute;
	background-color:rgba(249, 249, 249, 1);
	width:225px;
	min-width:225px;
	max-width:225px;
	height:calc( 100% );
	display:flex;
	flex-direction:column;
	overflow:hidden;
	z-index:4;
	/*box-shadow: 0px 0px 10px 5px rgba(128,128,128,0.4);*/

	box-shadow: 0 25.6px 57.6px 0 rgba(0, 0, 0, 0.22), 0 4.8px 14.4px 0 rgba(0, 0, 0, 0.18);
	transition: all 100ms;
}

/* hamburger menu - all sizes */
.main-all-resp-closed
{
	/*border: solid 1px blue;*/
	position:absolute;
	top:0px;
	bottom:0px;
	width:0px;
	min-width:0px;
	max-width:0px;
	height:calc( 100% );
	overflow:hidden;
	z-index:4;

	animation-delay: 0ms;
	animation-direction:reverse;
	animation-duration:100ms;
	animation-fill-mode:forwards;
	animation-iteration-count:1;
	animation-name:ResponsiveHamburgerNavAnimation;
	animation-play-state:running;
	animation-timing-function:ease-in-out;

}
.main-all-resp-open
{
	/*border: solid 1px red;*/	
	background-color:rgba(249, 249, 249, 1);
	border-right: solid 1px  rgba(208, 208, 208, 1);
	/*box-shadow: 5px 0px 10px 0px rgba(0,0,0,0.2);*/
	
	box-shadow: 0 25.6px 57.6px 0 rgba(0, 0, 0, 0.22), 0 4.8px 14.4px 0 rgba(0, 0, 0, 0.18);
	/*width:225px;
	max-width:225px;*/

	animation-delay: 0ms;
	animation-direction:normal;
	animation-duration:100ms;
	animation-fill-mode:forwards;
	animation-iteration-count:1;
	animation-name:ResponsiveHamburgerNavAnimation;
	animation-play-state:running;
	animation-timing-function:ease-in-out;
}
@keyframes ResponsiveHamburgerNavAnimation
{
	100%
	{
		/*background-image:  linear-gradient( 90deg, rgba(249, 249, 249, 0.0) 0px, rgba(249, 249, 249, 1) 225px);
		box-shadow: 0px 4px 10px 4px rgba(128,128,128,0.4);*/
		width:225px;
		min-width:225px;
		max-width:225px;
	}
}


/* large responsive panels */
.main-nav-resp-panel-closed
{
	border: solid 0px red;
	background-color:rgba(249, 249, 249, 1);
	background-image:  linear-gradient( 90deg, rgba(249, 249, 249, 1) 42px, rgba(208, 208, 208, 1) 48px);
	border-right: solid 0px  rgba(208, 208, 208, 1);
	box-shadow: 0px 4px 10px 4px rgba(128,128,128,0);

	position:absolute;
	top:40px;
	bottom:0px;
	width:48px;
	min-width:48px;
	max-width:48px;
	height:calc( 100% - 40px );
	overflow:hidden;
	z-index:4;

	animation-delay: 0ms;
	animation-direction:reverse;
	animation-duration:100ms;
	animation-fill-mode:forwards;
	animation-iteration-count:1;
	animation-name:OpenMainNav;
	animation-play-state:running;
	animation-timing-function:ease-in-out;
}
.main-nav-resp-panel-open
{
	animation-delay: 0ms;
	animation-direction:normal;
	animation-duration:100ms;
	animation-fill-mode:forwards;
	animation-iteration-count:1;
	animation-name:OpenMainNav;
	animation-play-state:running;
	animation-timing-function:ease-in-out;
}
@keyframes OpenMainNav
{
	100%
	{
		background-image:  linear-gradient( 90deg, rgba(249, 249, 249, 0.0) 0px, rgba(249, 249, 249, 1) 225px);
		box-shadow: 0px 4px 10px 4px rgba(128,128,128,0.4);
		width:225px;
		min-width:225px;
		max-width:225px;
	}
}

/* default non-responsive chevron styles */
.chevron-btn-panel-open
{
	/*border: solid 1px red;*/
	height: 24px;
	text-align:right;
}
.chevron-btn-panel-open:hover, .chevron-btn-panel-closed:hover
{
	background-color:rgba(85,179,255,0.1);
	cursor:pointer;
}
.chevron-btn-panel-open > svg
{
	height:8px;
	width:8px;
	margin-right:6px;
	margin-top:8px;
}
.chevron-btn-panel-closed
{
	/*border: solid 1px red;*/
	height: 24px;
	text-align:center;
}
.chevron-btn-panel-closed > svg
{
	height:8px;
	width:8px;
	margin-top:8px;
}

/* during pinned state */
.hamburger-action-panel
{
	order: 0;
	border: solid 0px red;
	flex: 0 0 28px;
	height:40px;
	padding-top:6px;
}
.hamburger-button 
{
	border: solid 0px red;
	cursor:pointer;
	text-align:center;
	height:28px;
	width:48px;
}
.hamburger-button > svg
{
	border: solid 0px red;
	margin-top:6px;
	height:16px;
	width:16px;
}
.hamburger-button:hover > svg
{
	fill:rgba(255,0,0,1);
}

.main-nav-list-panel
{
	border: solid 0px red;
	order:1;
	flex: 1 1 auto;
	display:block;
	height:calc(100%);
}

/* left nav items in both states */
.left-nav-ext-list-panel
{
	order:2;
	/*border: solid 1px red;*/
	overflow-x:hidden;
	overflow-y:auto;
	display:block;
	width:100%;
	height:calc(100%  -  158px);
}
.left-nav-ext-list-panel-closed
{
	overflow-y:hidden;
	overflow-x:hidden;
}

.main-nav-resp-panel > .left-nav-ext-list-panel
{
	border: solid 0px purple;
	height:calc(100%  -  184px);
}

/* style changes for <cards/> */
.left-nav-button-with-card
{
	/* just needed for debugging */
	/*border: solid 1px red;*/
	padding:0;
	margin:0;
}


/* main-nav & resrouce-nav use this */
.left-nav-btn 
{
	/*border: 1px solid red;*/
	border: solid 0px transparent;
	border-left: solid 4px #F3F2F1;
	/*background-color: rgba(249, 249, 249, 0);*/
	color: rgba(0,0,0,1);
	display: flex;
	padding: 0px;
	cursor: pointer;
	height: 32px;
	vertical-align: top;
	text-decoration: none;
	width:100%;
}
.left-nav-btn:hover
{
	/*border: solid 1px red;*/
	background-color: #F3F2F1;
	background-image:  linear-gradient( 90deg,#F3F2F1 calc(100% - 10px), rgba(208, 208, 208, 0.8) calc(100% ));
	/*box-shadow: 0 6.4px 14.4px 0 rgba(0, 0, 0, 0.132), 0 1.2px 3.6px 0 rgba(0, 0, 0, 0.108);*/
}
.left-nav-btn-selected
{
	background-color: #EDEBE9;
	border-left: solid 4px #0078D4;
	background-image:  linear-gradient( 90deg, rgba(238, 238, 238, 0.6) calc(100% - 8px), rgba(208, 208, 208, 0.6) calc(100% ));
}
.left-nav-btn-selected:hover
{
	background-color: #E1DFDD;
}
.left-nav-btn:focus
{
	background-color: rgba(85,179,255,0.1);
	outline: 1px dashed rgba(85,179,255,0.1);
}

.left-nav-icon
{
	border: solid 0px green;
	flex: 0 0 44px;
	vertical-align:top;
	text-align:center;
}
.left-nav-icon > svg
{
	/*border: solid 1px blue;*/
	width: 18px;
	height: 18px;
	vertical-align:top;
	text-align:center;
	margin-top:8px;
	margin-left:0px;
}
.left-nav-res-name
{
	/*border: 1px solid red;*/ 
	flex: 1 0 175px;
	width:100%;
	text-align:left;
	vertical-align:top;
	color:rgba(0,0,0,1);
	padding-top:7px;
	padding-left:0px;
	padding-right:4px;
	padding-bottom:4px;	
	text-overflow:ellipsis;
	white-space: nowrap; 
	overflow:hidden;
}

/* favorites */
.main-nav-favorites
{	/*border: 1px solid red;*/ 
	height:30px;
}

/* mixed menu for responsive */
.shared-menu-panel
{
	/*border: solid 1px red;*/
	display:flex;
	flex-direction:column;
	height: calc(100% - 40px);
}
.shared-menu-toggle-panel
{
	/*border: solid 1px red;*/
	border-bottom:solid 1px rgba(128,128,128,0.2);
	/*margin-bottom:10px;*/
	padding-bottom:10px;
	order:0;
	height:auto;
}
.shared-menu-content-panel
{
	/*border: solid 1px red;*/
	order: 1;
	overflow:auto;
}


/* responsive, also see control code for state & prop values */
@media screen and (max-width: 1023px)
{
	.main-nav-open:not(.main-nav-closed)
	{
		/*border: solid 1px red;*/
		position:absolute;
		background-color: rgba(249,249,249,1);
		height:calc(100% - 40px);
		box-shadow: 0px 0px 10px 5px rgba(128,128,128,0.4);
		transition: all 100ms;
	}

	.left-nav-ext-list-panel
	{
		border: solid 0px red;
		height:calc(100%);
		overflow:hidden;
	}

	.left-nav-ext-list-panel > .left-nav-ext-list-panel-closed
	{
		border: solid 0px red;
		height: calc(100% - 198px);
		overflow-x:hidden;
		overflow-y:auto;
	}

	.main-nav-closed > .main-nav-list-panel > .left-nav-btn-selected
	{
		/*border: solid 1px red;*/
		background-image:  linear-gradient( 90deg, rgba(238, 238, 238, 0.6) calc(100% - 8px), rgba(208, 208, 208, 0.6) calc(100% ));
	}

	.main-nav-open:not(.main-nav-closed) > .main-nav-list-panel > .left-nav-btn-selected
	{
		/*border: solid 1px red;*/
		background-image: none;
	}
	.left-nav-btn-selected:hover
	{
		/*border: solid 1px red;*/
		background-image:  linear-gradient( 90deg, rgba(238, 238, 238, 0.6) calc(100% - 8px), rgba(208, 208, 208, 0.6) calc(100% ));
	}
	.left-nav-btn:hover
	{
		/*border: solid 1px red;*/
		background-color: rgba(238, 238, 238, 1);
		background-image: none;
	}
}

@media screen and (max-width: 639px)
{
	.main-nav-resp-panel-closed
	{
		/*border: solid 1px red;*/
		min-width:0px;
		max-width:0px;
		width:0px;
	}
	.main-nav-resp-panel-open
	{
		top:0px;
		height:calc( 100% );
	}
	.left-nav-ext-list-panel
	{
		border: solid 0px red;
		height:calc(100% - 40px);
		overflow:hidden;
	}
	.left-nav-ext-list-panel > .left-nav-ext-list-panel-closed
	{
		border: solid 0px red;
		height: calc(100% - 156px);
		overflow-x:hidden;
		overflow-y:auto;
	}
	.shared-menu-content-panel > .left-nav-ext-list-panel
	{
		height:100%;
	}
}
@media screen and (max-width: 479px)
{
	@keyframes ResponsiveHamburgerNavAnimation
	{
		100%
		{
			width:320px;
			min-width:320px;
			max-width:320px;
		}
	}
}
@media screen and (max-width: 379px)
{
	@keyframes ResponsiveHamburgerNavAnimation
	{
		100%
		{
			width:100%;
			min-width:100%;
			max-width:100%;
		}
	}
}