.feedback-panel
{
	/*border: solid 1px red;*/
	margin:20px;
	margin-top:10px;
	height: calc(100% - 40px);
	width: calc(100% - 40px);
	font-size:12px;
}

.feedback-icon-row
{
	/*border: solid 1px red;*/
	display:flex;
	margin-top:10px;
}
.feedback-icon
{
	border: solid 1px rgba(255,0,0,0);
    border-radius:4px;
	flex: 0 0 32px;
	height:32px;
	width:32px;
	margin-right:10px;
	padding:5px;
	cursor:pointer;
}
.feedback-icon > svg
{
	height:100%;
	width:100%;
}
.feedback-icon:hover
{
    border-color: rgba(0, 88, 173, 0.5);
	background-color: rgba(215, 236, 249, 0.5);
}
.feedback-icon:hover > svg
{
	fill: rgba(0, 88, 173, 1);
}
.feedback-icon-selected
{
    border-color: #0058ad;
	background-color: #d7ecf9;
}
.feedback-icon-selected > svg
{
	fill: #0058ad;
}

.feedback-textarea
{
	width: calc(100%);
	height:calc(200px);
	font-family: 'az_Regular';
	padding:3px;
	font-size:12px;
	resize:none;
}

.feedback-pii
{
	padding-top:10px;
	cursor:pointer;
}