.bread-crumb-btn
{
	border: solid 0px red;
	display:inline-block;
	height:28px;
	width:auto;
}

.bread-crumb-text
{
	border: solid 0px red;
	display:inline-flex;
	font-size:13px;
	vertical-align:top;
	height:100%;
}

.bc-text
{
	border: solid 0px red; 
	flex: 1 1 auto;
	color: rgba(0, 108, 215,1);
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
	height:100%;
	padding:5px;
}
.bc-text:hover
{
	color: rgba(0, 78, 185,1);
	background-color:rgba(85,179,255, 0.2);
	cursor:pointer;
}

.bc-divider
{
	border: solid 0px red; 
	flex: 0 0 10px;
	width:10px;
	padding-top:5px;
	color:rgba(0,0,0,1);
}

.bread-crumb-text-no-link
{
	display:inline-block;
	height:100%;
	font-size:13px;
	vertical-align:top;
	padding:5px;
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
	cursor:pointer;
}

@media screen and (max-width: 1023px)
{
	.bc-text
	{
		border: solid 0px rgba(255,0,0,1);
		max-width:60px;
	}
	.bc-divider
	{
		border: solid 0px red; 
		flex: 0 0 10px;
		width:10px;
		/*color:rgba(0,0,0,1);*/
	}
}
